.users {
  border: 1px solid purple;
  background-color: #fff;

  width: 55%;
  margin: auto;

  color: black;
  list-style-type: none;
}

.users ul {
  list-style: none;
  padding: 0.5rem;
}

.users li {
  border: 1px solid #ccc;
  margin: 0.5rem 0;
  padding: 0.5rem;
}

h2 {
  text-align: center;
  font-family: Calibri Light, sans-serif;
}
li {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
button .delete-btn {
  border: 0;
}
img {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 16px;
  width: 16px;
}
